.wei-table {
  margin-top: 3%;
}
.wei-table .ant-card-body {
  padding: 0;
}
.wei-table .ant-table-thead > tr > th {
  white-space: nowrap;
}
.wei-table .ant-table-row td {
  white-space: nowrap;
}
.wei-table .detailed-goback {
  height: 18px;
  font-size: 14px;
  color: #0091ff;
  line-height: 18px;
  cursor: pointer;
  margin-bottom: 20px;
}
.wei-table .personal-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 32px;
  color: #000;
}
